header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}
 

/******CTA*****/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/********header socials******/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 2px;
    height: 2rem;
    background: var(--color-primary);
}

/******IMG********/

.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 21rem;
    height: 20rem;
    position: absolute;
    left: 34%;
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 0.1rem ;

}

img{
    height: 100%;
    object-fit: cover;
    border-radius: 12rem 12rem 0 0;
}

/******SCROLL********/
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ****** MEDIA-QUERY(MEDIUM DEVICES) **** */
@media screen and (max-width: 1024px){
    header{
        height: 55vh;
    }
    .me{
        left:25%;
    }
    
} 

/* ****** MEDIA-QUERY(SMALL DEVICES) **** */
@media screen and (max-width: 600px){
    header{
        height: 75vh;
        
    }
    .header__socials , .scroll__down{
        display: none;
    }
    .me{
        left:5%;
        width: 16rem;
        height: 16rem;
        margin-left: 2rem;
    }
}